@import './variable.scss';

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  line-height: 1;
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  background: $blue;
}

.container {
  margin: 0 auto;
  width: 90%;
  max-width: 1440px;
}
